.page {
  font-family: "Space Grotesk", sans-serif;
  font-size: 16px;
  line-height: 140%;
  color: #292929;
  scroll-behavior: smooth;

  &__body {
    margin: 0;
    min-width: 320px;
  }

  &__menu {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;

    opacity: 0;
    transition: all, 0.3s;
    transform: translateX(-100%);
    pointer-events: none;

      &:target {
      transform: translateX(0);
      opacity: 1;
      pointer-events: all;
    }
  }
}



